<template>
  <el-container class="home-container">
    <!-- 头部区域 -->
    <el-header>
      <div>
        <img src="../assets/logo.png" alt />
        <span>电商后台管理系统</span>
      </div>
      <el-button type="info" @click="logout">退出</el-button>
    </el-header>
    <!-- 页面主体区域 -->
    <el-container>
      <!-- 侧边栏 -->
      <el-aside :width="isCollapse?'64px':'200px'">
        <!-- 能隐藏左侧菜单的按钮 -->
        <div class="toggle-button" @click="toggleCollapse">|||</div>
        <!-- 侧边栏菜单区域 unique-opened决定了是否只能打开一个子菜单,布尔类型 写上了就是true的意思 unique-opened等同于 :unique-opened="true"  -->
        <el-menu
          background-color="#333744"
          text-color="#fff"
          active-text-color="#409EFF"
          unique-opened
          :collapse="isCollapse"
          :collapse-transition="false"
          router
          :default-active="this.$route.path"
        >
          <!-- 一级菜单 前面加:号 表示是动态接收 :index 加个'' 就是给他转成字符串 item.id的值是数值 :index只接收字符串 这是隐式转换-->
          <el-submenu :index="item.id + '' " v-for="item in menulist" :key="item.id">
            <!-- 一级菜单的模板区域 -->
            <template slot="title">
              <!-- 图标 -->
              <i :class="iconsObj[item.id]"></i>
              <!-- 文本 -->
              <span>{{item.authName}}</span>
            </template>
            <!-- 二级菜单 -->
            <el-menu-item :index="'/' + subItem.path" v-for="subItem in item.children" :key="subItem.id">
              <!-- 图标 -->
              <i class="el-icon-menu"></i>
              <!-- 文本 -->
              <span>{{subItem.authName}}</span>
            </el-menu-item>
          </el-submenu>
          
        </el-menu>
      </el-aside>
      <!-- 右侧内容主题栏 -->
      <el-main>
        <!-- 路由的占位符 -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data(){
    return{
      //左侧菜单数据
      menulist:[],
      iconsObj:{
        '125':'el-icon-user',
        '103':'el-icon-check',
        '101':'el-icon-goods',
        '102':'el-icon-s-order',
        '145':'el-icon-s-marketing',
      },
      //是否折叠 false表示不折叠
      isCollapse:false
    }
  },
  //生命周期函数
  created(){
    this.getMenuList()
  },
  methods: {
    logout() {
      window.sessionStorage.clear();
      this.$router.push("/login");
    },
    //获取所有的菜单
    async getMenuList(){
      //const result = await this.$http.get('menus');
      const {data:result} = await this.$http.get('menus');
      if(result.meta.status!==200) return this.$message.console.error(result.meta.msg);
      this.menulist=result.data;
      //console.log(result);
    },
    //点击按钮 切换菜单的折叠于展开
    toggleCollapse(){
      this.isCollapse = !this.isCollapse;
    }
  }
};
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
}
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;
  color: #ffffff;
  font-size: 20px;
  > div {
    display: flex;
    align-items: center;
    span {
      margin-left: 15px;
    }
  }
}
.el-aside {
  background-color: #333744;
  .el-menu{
    border-right: none;
  }
}

.el-main {
  background-color: #eaedf1;
}

.toggle-button{
  background-color: #4a5064;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  // letter-spacing是给字符之间加空格
  letter-spacing: 0.2em;
  //让鼠标放上去呈现出小手状态
  cursor: pointer;
}
</style>